.container{
    margin: 20px;
}
.profile{
    width:200px;
}

.header{
    flex-direction: row;
    justify-content: center;
    text-align: center;
}

.name{
    font-size: 30px;
    font-family: 'Noto Sans KR';
    font-weight: 700;
}

.name2{
    font-size: 20px;
    margin-bottom: 25px;
}

.menuBar{
    width: 80%;
    font-size: 15px;
    font-weight: 550;
    display: flex;
    justify-content: center;
    background-color: rgb(0, 0, 0);
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border-radius: 10px;
    margin: 0 auto;
}

.intro, .timeLine, .portfolio, .bar{
    margin: 10px 30px 10px 30px;   
    text-decoration: none; 
    color: rgb(255, 255, 255);
}


