
.container{
    margin: 0 auto;
    width: 800px;
    border-bottom: 1px rgb(85, 85, 85) solid;
}
.numbering{
    font-size: 15px;
    font-weight: bolder;
    border: 0px black solid;
    padding: 5px 10px;
    border-radius: 50%;
    /* background-color: rgb(221, 211, 191); */
}

.contentsHeader{
    display: flex;
    text-align: center;
    margin-top: 15px;

}

.title{
    font-size: 15px;
    margin: 5px 0px 0px 10px;
    font-weight: bolder;
}

img{
    width: 500px;
}

.scrollarea{
    margin: 0px 0px 15px 25px;
    font-size: 13px;
    font-weight: 400;
    line-height:21px;
    /* background-color: rgb(238, 238, 238); */
    /* border-radius: 30px; */
    padding: 15px;
    height: 250px;
    overflow: auto;
}

.contents::-webkit-scrollbar {
    width: 10px;
  }
  .contents::-webkit-scrollbar-thumb {
    background-color: #2f3542;
    border-radius: 10px;
    background-clip: padding-box;
    border: 2px solid transparent;
  }
  .contents::-webkit-scrollbar-track {
    background-color: grey;
    border-radius: 10px;
    box-shadow: inset 0px 0px 5px white;
  }
.main{
    display: flex;
    margin: 20px 0px 20px 0px;
    align-items: center;
    justify-content: center;
    
}

.button{
    border-radius: 15px;
    margin: 0px 0px 0px 10px;
    padding: 4px 5px;
    background-color: black;
    color: white;
    cursor: pointer;
    font-size: 10px;
}